





























































































































































































.alpheios-alignment-editor-modal-options {
  .alpheios-modal-container {
    width: 700px;
  }
  .alpheios-modal-body {
    // max-height: 700px;
    margin: 0 0 20px;

    border: 0;
    border-top: 2px solid #ddd;
    padding: 10px 0 0 0;
  }

  .alpheios-alignment-editor-modal-options-block {
      padding: 10px;
  }
  .alpheios-alignment-editor-modal-header {
    margin-bottom: 10px;
  }

  .alpheios-alignment-options__buttons {
    margin: 0;
  }

  .alpheios-alignment-editor-modal-options-block-fieldset {
    margin: 0 0 20px;
    border-color: #eeee;
    border-width: 1px;
    border-image: none;
    padding: 10px 10px 0;
  }

  .alpheios-options-expander-label {
    display: inline-block;
    cursor: pointer;
    width: 100%;
    background: #eee;
    padding: 10px;
    border-radius: 2px;

    i {
      display: block;
      float: right;
      margin: 10px;
      border-color: #555;
      border-width: 0 4px 4px 0;
      padding: 4px;
    }

    &:before,
    &:after {
      clear: both;
      display: table;
      content: '';
    }
  }

  .alpheios-alignment-options__fieldset-group {
    transition: all 1s;
    overflow: hidden;
    &.alpheios-collapsed {
      height: 0;
    }
    &.alpheios-alignment-options__fieldset-group_academic.alpheios-expanded {
      height: 175px;
    }

    &.alpheios-alignment-options__fieldset-group_advanced.alpheios-expanded {
      height: 465px;
    }
  }

}
