.alpheios-alignment-editor-align-text-segment .alpheios-alignment-editor-align-text-segment-tokens {
  padding: 10px;
  max-height: 400px;
  overflow-y: scroll;
}

.alpheios-alignment-editor-align-text-segment-row {
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 2px;
  border-bottom: 0;
  margin: 0;
  padding: 0;
  font-size: 90%;
  background: #eee;
}
.alpheios-alignment-editor-align-text-segment-row .alpheios-alignment-editor-align-text-segment-row__langname {
  display: inline-block;
  padding: 5px 5px 0 15px;
  color: #000;
  font-weight: bold;
}

span.alpheios-alignment-editor-text-blocks-single__icons {
  display: block;
  padding: 5px;
}
span.alpheios-alignment-editor-text-blocks-single__icons svg {
  display: block;
  width: 25px;
  height: 25px;
  stroke: #99002a;
  fill: transparent;
}

span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data {
  cursor: pointer;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data svg {
  stroke: #99002a;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data {
  cursor: pointer;
}
span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data svg {
  stroke: #2a9900;
}

.alpheios-alignment-editor-align-text-parts-link-text {
  cursor: pointer;
  display: inline-block;
  padding: 3px;
  text-decoration: underline;
}

.alpheios-alignment-editor-align-text-parts-link {
  display: inline-block;
  vertical-align: middle;
  padding: 3px;
  font-size: 0;
  background: #c6c6c6;
  border: 1px solid #fff;
  min-width: 8px;
}
.alpheios-alignment-editor-align-text-parts-link.alpheios-alignment-editor-align-text-parts-link-current {
  cursor: initial;
  background: #6e6e6e;
}

.alpheios-alignment-editor-align-text-parts,
.alpheios-alignment-editor-align-text-single-link {
  margin: 0;
  text-align: center;
  color: #000;
  font-weight: bold;
}

.alpheios-alignment-editor-align-text-parts {
  padding: 0 25px;
}