





















































































.alpheios-alignment-editor-align-target-tabs {
  display: inline-block;
  padding-left: 0;
  position: relative;
  width: 49%;
  vertical-align: middle;
  padding-left: 10px;
}
.alpheios-alignment-editor-align-target-tab-item {
  cursor: pointer;
  display: inline-block;

  border-radius: 30px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;

  margin: 0 10px 0 0;

  background: #bebebe;
  color: #fff;

  &.alpheios-alignment-editor-align-target-tab-item-active {
    background: #185F6D;
  }
}
