



















































































.alpheios-alignment-editor-modal-insert-tokens {
  .alpheios-modal-body {
      border: 0;
      padding: 0;
  }

  .alpheios-alignment-radio-block {
    margin-top: 0;
    margin-bottom: 15px;
    span {
      display: inline-block;
      margin-right: 20px;
      vertical-align: middle;
    }
  }

  .alpheios-alignment-editor-text-blocks-textarea {
    padding: 10px 55px 10px 10px;
    width:100%;
    min-height: 100px;
    font-size: inherit;
  }
}
