.alpheios-alignment-app-menu {
  height: 100%;
  width: 250px;
  position: fixed;
  z-index: 10000;
  top: 0;
  left: -250px;
  background-color: #e0e0e0;
  overflow-x: hidden;
  transition: left 0.5s;
}
.alpheios-alignment-app-menu.alpheios-shown {
  left: 0;
}
.alpheios-alignment-app-menu .alpheios-alignment-app-menu__buttons {
  padding: 70px 20px 10px;
}
.alpheios-alignment-app-menu .alpheios-alignment-app-menu-close-icon {
  display: block;
  position: absolute;
  top: 25px;
  right: 25px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.alpheios-alignment-app-menu .alpheios-alignment-app-menu-close-icon svg {
  width: 100%;
  height: 100%;
  display: block;
}

.alpheios-app-menu-link {
  display: block;
  padding: 7px 0;
  cursor: pointer;
  white-space: nowrap;
}

.alpheios-alignment-app-menu__upload-block {
  text-align: left;
  padding: 10px 0;
  border-top: 2px solid #ddd;
  border-bottom: 2px solid #ddd;
}

.alpheios-main-menu-upload-block-radio-block_item,
.alpheios-main-menu-upload-block_item {
  padding-right: 20px;
}

.alpheios-main-menu-upload-block_item.alpheios-token-edit-actions-button {
  width: 30px;
  height: 30px;
  border-radius: 30px;
  line-height: 30px;
  padding: 5px;
}

button.alpheios-app-menu-link {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
  overflow: visible;
  text-transform: none;
  -webkit-appearance: button;
  background: transparent;
  border: 0;
  outline: 0;
}
button.alpheios-app-menu-link.alpheios-app-menu-link-current {
  font-weight: bold;
  text-shadow: 1px 1px #eee;
}

button.alpheios-app-menu-link::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button.alpheios-app-menu-link:-moz-focusring {
  outline: 1px dotted ButtonText;
}

.alpheios-fileupload {
  opacity: 0;
  width: 0.1px;
  height: 0.1px;
  position: absolute;
}

.alpheios-fileupload-label {
  display: inline-block;
}

.alpheios-alignment-app-menu__buttons-blocks {
  border-bottom: 2px solid #eee;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.alpheios-app-black-screen {
  height: 100%;
  width: 100%;
  position: fixed;
  z-index: 90;
  top: 0;
  left: 0;
  opacity: 0.6;
  background: #000;
  transition: 0.6s;
}