.alpheios-alignment-editor-initial-screen__intro {
  background-repeat: repeat-y;
  background-position: 98% 20%;
  background-size: 20% auto;
  min-height: 100vh;
}

.alpheios-alignment-editor-initial-screen__heading {
  display: none;
  margin: 20px 60px;
  color: #0E2233;
  font-size: 80px;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.6);
  font-family: "Source Serif Pro", serif;
  text-align: center;
}

.alpheios-alignment-editor-initial-screen__video {
  text-align: center;
}
.alpheios-alignment-editor-initial-screen__video iframe {
  margin: 0;
  border: 0;
  width: 800px;
  height: 500px;
}

.alpheios-header-logo {
  text-align: center;
  padding-top: 80px;
  height: 60px;
}
.alpheios-header-logo .alpheios-logo {
  display: inline-block;
  width: 250px;
}

.alpheios-alignment-editor-initial-screen__buttons {
  width: 1000px;
  margin: 0 auto 50px;
  text-align: center;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button {
  width: 49%;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-actions-menu-main-button {
  font-weight: normal;
  font-size: 22px;
  min-width: 315px;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block {
  text-align: center;
  margin-top: 10px;
  border-color: #6e7a84;
  border-width: 1px;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block .alpheios-main-menu-upload-block_item {
  padding: 0;
}
.alpheios-alignment-editor-initial-screen__buttons .alpheios-alignment-editor-initial-screen__button .alpheios-alignment-app-menu__upload-block .alpheios-actions-menu-button-upload {
  background: #eee;
  color: #46788d;
  font-weight: normal;
}