.alpheios-alignment-editor-modal-options .alpheios-modal-container {
  width: 700px;
}
.alpheios-alignment-editor-modal-options .alpheios-modal-body {
  margin: 0 0 20px;
  border: 0;
  border-top: 2px solid #ddd;
  padding: 10px 0 0 0;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-editor-modal-options-block {
  padding: 10px;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-editor-modal-header {
  margin-bottom: 10px;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-options__buttons {
  margin: 0;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-editor-modal-options-block-fieldset {
  margin: 0 0 20px;
  border-color: #eeee;
  border-width: 1px;
  border-image: none;
  padding: 10px 10px 0;
}
.alpheios-alignment-editor-modal-options .alpheios-options-expander-label {
  display: inline-block;
  cursor: pointer;
  width: 100%;
  background: #eee;
  padding: 10px;
  border-radius: 2px;
}
.alpheios-alignment-editor-modal-options .alpheios-options-expander-label i {
  display: block;
  float: right;
  margin: 10px;
  border-color: #555;
  border-width: 0 4px 4px 0;
  padding: 4px;
}
.alpheios-alignment-editor-modal-options .alpheios-options-expander-label:before, .alpheios-alignment-editor-modal-options .alpheios-options-expander-label:after {
  clear: both;
  display: table;
  content: "";
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-options__fieldset-group {
  transition: all 1s;
  overflow: hidden;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-options__fieldset-group.alpheios-collapsed {
  height: 0;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-options__fieldset-group.alpheios-alignment-options__fieldset-group_academic.alpheios-expanded {
  height: 175px;
}
.alpheios-alignment-editor-modal-options .alpheios-alignment-options__fieldset-group.alpheios-alignment-options__fieldset-group_advanced.alpheios-expanded {
  height: 465px;
}