.alpheios-alignment-editor-container {
  padding: 15px 15px 0;
}
.alpheios-alignment-editor-container h2 {
  margin: 0;
  padding: 0;
}

.alpheios-alignment-app-menu-open-icon {
  display: block;
  position: fixed;
  top: 25px;
  left: 10px;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.alpheios-alignment-app-menu-open-icon svg {
  width: 100%;
  height: 100%;
  display: block;
}