.alpheios-alignment-editor-app-container {
  .alpheios-modal-mask {
      position: fixed;
      z-index: 9998;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
      display: table;
      transition: opacity 0.3s ease;

      // padding-top: 25%;
    }
    
    .alpheios-modal-wrapper {
      display: table-cell;
      vertical-align: middle;
    }
    
    .alpheios-modal-container {
      width: 600px;
      max-height: 600px;
      margin: 0px auto;
      padding: 20px 30px;
      background-color: #fff;
      border-radius: 2px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
      transition: all 0.3s ease;
    }

    .vm--container {
      z-index: 99999;
    }

    .vm--modal {
      padding: 20px 30px;
      background-color: #fff;
      border-radius: 2px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
    }

    .alpheios-modal-header h3 {
      margin-top: 0;
      color: #42b983;
    }
    
    .alpheios-modal-body {
      margin: 10px 0;
      max-height: 400px;
      overflow: auto;

      border: 2px solid #ddd;
      padding: 10px;
    }
    
    .alpheios-modal-default-button {
      float: right;
    }
    
    /*
    * The following styles are auto-applied to elements with
    * transition="modal" when their visibility is toggled
    * by Vue.js.
    *
    * You can easily play with the modal transition by editing
    * these styles.
    */
    
    .alpheios-modal-enter {
      opacity: 0;
    }
    
    .alpheios-modal-leave-active {
      opacity: 0;
    }
  /*
    .alpheios-modal-enter .alpheios-modal-container,
    .alpheios-modal-leave-active .alpheios-modal-container {
      -webkit-transform: scale(1.1);
      transform: scale(1.1);
    }
  */
  .alpheios-modal-header {
    position: relative;

    .alpheios-alignment-modal-close-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        width: 25px;
        height: 25px;
        cursor: pointer;

        svg {
          width: 100%;
          height: 100%;
          display: block;
          fill: #c6c6c6;
        }
    }
  }
}
