.alpheios-alignment-editor-hidden {
  visibility: hidden;
}

.alpheios-alignment-editor-text-blocks-single textarea {
  width: 100%;
  min-height: 300px;
  font-size: inherit;
}
.alpheios-alignment-editor-text-blocks-single p {
  margin-bottom: 10px;
}
.alpheios-alignment-editor-text-blocks-single p.alpheios-alignment-editor-text-blocks-info-line {
  margin: 0;
}
.alpheios-alignment-editor-text-blocks-single p.alpheios-alignment-editor-text-blocks-info-line:before, .alpheios-alignment-editor-text-blocks-single p.alpheios-alignment-editor-text-blocks-info-line:after {
  clear: both;
  content: "";
  display: table;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__characters {
  color: #888;
  font-size: 90%;
  display: block;
  float: left;
  padding: 10px 0;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons {
  display: block;
  float: right;
  padding: 5px;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons svg {
  display: block;
  width: 30px;
  height: 30px;
  stroke: #99002a;
  fill: transparent;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data {
  cursor: pointer;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_no_data svg {
  stroke: #99002a;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data {
  cursor: pointer;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-text-blocks-single__icons.alpheios-alignment-editor-text-blocks-single__metadata_icon_has_data svg {
  stroke: #2a9900;
}
.alpheios-alignment-editor-text-blocks-single span.alpheios-alignment-editor-red {
  color: #99002a;
}
.alpheios-alignment-editor-text-blocks-single .alpheios-alignment-editor-text-blocks-single__lang-icon {
  display: block;
  float: right;
  font-weight: bold;
  padding: 3px;
  border: 2px solid #000;
  border-radius: 30px;
  font-size: 13px;
  margin-top: 5px;
  margin-left: 3px;
  cursor: pointer;
  min-width: 33px;
  text-align: center;
}
.alpheios-alignment-editor-text-blocks-single .alpheios-alignment-editor-text-blocks-single__lang-icon.alpheios-alignment-editor-text-blocks-single__lang-icon_disabled {
  cursor: initial;
  color: #9a9a9a;
  border-color: #9a9a9a;
}

.alpheios-alignment-editor-text-blocks-single__title {
  position: relative;
  font-size: 20px;
  font-weight: bold;
}
.alpheios-alignment-editor-text-blocks-single__title.alpheios-alignment-editor-text-blocks-single__title_less-margin {
  margin-bottom: 7px;
}

.alpheios-alignment-editor-add-translation {
  display: inline-block;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.alpheios-alignment-editor-add-translation svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.alpheios-alignment-editor-options-fieldset {
  padding: 10px;
  border: 2px groove #f8f8f8;
  margin-bottom: 30px;
}
.alpheios-alignment-editor-options-fieldset legend {
  display: block;
  padding: 0 10px;
  margin-bottom: 10px;
  line-height: inherit;
  color: inherit;
  white-space: normal;
  font-size: 110%;
}

.alpheios-alignment-editor-options-fieldset-slim {
  padding: 0;
  margin-bottom: 0;
  border: 0;
}
.alpheios-alignment-editor-options-fieldset-slim legend {
  display: none;
}

.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-option-item__label {
  width: auto;
  margin-right: 20px;
}
.alpheios-alignment-editor-options-fieldset-label-auto input.alpheios-alignment-input,
.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-select,
.alpheios-alignment-editor-options-fieldset-label-auto .alpheios-alignment-radio-block {
  width: auto;
}

.alpheios-alignment-editor-text-blocks-single__type-label {
  display: inline-block;
  padding-right: 15px;
}

.alpheios-alignment-editor-text-blocks-single__title {
  position: relative;
}
.alpheios-alignment-editor-text-blocks-single__title .alpheios-alignment-editor-text-blocks-single__title-text {
  display: inline-block;
  padding-right: 15px;
  line-height: 35px;
}
.alpheios-alignment-editor-text-blocks-single__title .alpheios-alignment-editor-add-translation {
  position: relative;
  top: 5px;
}

.alpheios-alignment-editor-text-blocks-textarea {
  padding: 10px 55px 10px 10px;
}

.alpheios-alignment-editor-text-blocks-single-text-area-container {
  position: relative;
}
.alpheios-alignment-editor-text-blocks-single-text-area-container .alpheios-alignment-editor-text-blocks-single__remove {
  top: 55px;
  right: 25px;
  fill: #ddd;
  display: inline-block;
  width: 20px;
  height: 20px;
  position: absolute;
  cursor: pointer;
}
.alpheios-alignment-editor-text-blocks-single-text-area-container .alpheios-alignment-editor-text-blocks-single__remove svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}

.alpheios-alignment-editor-text-blocks-single__align-button {
  text-align: right;
  display: inline-block;
  padding-left: 20px;
}

.alpheios-alignment-editor-text-blocks-single__describe-button {
  text-align: center;
  margin-top: 10px;
}