.alpheios-alignment-editor-alignments-table td {
  text-align: left;
  padding: 5px;
  cursor: pointer;
}
.alpheios-alignment-editor-alignments-table td.alpheios-alignment-editor-alignments-table_dt {
  min-width: 144px;
  text-decoration: underline;
}

.alpheios-alignment-editor-alignments-table_delete-icon_span {
  display: inline-block;
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.alpheios-alignment-editor-alignments-table_delete-icon_span svg {
  display: inline-block;
  width: 100%;
  height: 100%;
}