.alpheios-alignment-editor-align-text-segment span.alpheios-token {
  cursor: pointer;
  padding: 2px 2px 0 2px;
  margin-bottom: 2px;
  border: 1px solid transparent;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-part-shadowed {
  font-weight: bold;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token:hover {
  border-color: #FFC24F;
  background: #FFD27D;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-annotation-mode:hover {
  border-color: initial;
  background: initial;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-grouped {
  border-color: #BCE5F0;
  background: #BCE5F0;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-selected {
  border-color: #F27431;
  background: #F27431;
  color: #fff;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-clicked {
  border-color: #f59d6e;
  background: #f59d6e;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-clicked-first {
  border-color: #f06d26;
  background: #f06d26;
  color: #fff;
}
.alpheios-alignment-editor-align-text-segment span.alpheios-token.alpheios-token-annotated {
  border-bottom: 1px solid;
}