.alpheios-alignment-align-editor-block.alpheios-alignment-editor-container {
  padding: 20px 30px 20px 40px;
}

.alpheios-alignment-text-editor-block__part {
  min-width: 255px;
}
.alpheios-alignment-text-editor-block__part.alpheios-alignment-text-editor-block__part-2 {
  text-align: center;
}
.alpheios-alignment-text-editor-block__part.alpheios-alignment-text-editor-block__part-3 {
  text-align: right;
}

div.alpheios-alignment-annotation-mode-check-container {
  display: block;
  line-height: initial;
  max-width: initial;
  min-height: initial;
  padding-top: 5px;
}
div.alpheios-alignment-annotation-mode-check-container .alpheios-switch-label {
  font-weight: normal;
  font-size: 80%;
  padding-left: 5px;
  line-height: 22px;
}