div.alpheios-alignment-checkbox-block {
  display: inline-block;
  position: relative;
  overflow: hidden;
  line-height: textsize(22px);
  max-width: textsize(235px);
  vertical-align: top;
  min-height: 17px;
}

.alpheios-alignment-checkbox-block input[type=checkbox] {
  opacity: 0;
  position: absolute;
  left: -999px;
}
.alpheios-alignment-checkbox-block label {
  position: relative;
  display: inline-block;
  user-select: none;
  vertical-align: top;
  /*16px width of fake checkbox + 6px distance between fake checkbox and text*/
  padding-left: 25px;
  padding-top: 2px;
  line-height: 100%;
}
.alpheios-alignment-checkbox-block label::before, .alpheios-alignment-checkbox-block label::after {
  position: absolute;
  content: "";
  /*Needed for the line-height to take effect*/
  display: inline-block;
}
.alpheios-alignment-checkbox-block label::before {
  height: 16px;
  width: 16px;
  border: 1px solid;
  border-radius: 4px;
  left: 0;
  /*(24px line-height - 16px height of fake checkbox) / 2 - 1px for the border
   *to vertically center it.
   */
  top: 3px;
}
.alpheios-alignment-checkbox-block label::after {
  height: 5px;
  width: 9px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: 3px;
  top: 6px;
}
.alpheios-alignment-checkbox-block.alpheios-alignment-checkbox-block__disabled label {
  color: #8e8e8e;
}
.alpheios-alignment-checkbox-block input[type=checkbox] + label::after {
  content: none;
}
.alpheios-alignment-checkbox-block input[type=checkbox]:checked + label::after {
  content: "";
}

