  box-shadow: 0 0 1px #185F6D;
}
.alpheios-alignment-editor-app-container input:checked + .alpheios-slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}
