.alpheios-alignment-text-editor-block__header {
  font-size: 20px;
  display: flex;
  justify-content: space-between;
}
.alpheios-alignment-text-editor-block__header .alpheios-alignment-text-editor-block__part button {
  text-transform: uppercase;
}
.alpheios-alignment-text-editor-block__header .alpheios-alignment-text-editor-block__part-right {
  text-align: right;
}
.alpheios-alignment-text-editor-block__header .alpheios-alignment-text-editor-block__header-label {
  display: inline-block;
  padding-right: 15px;
  text-transform: uppercase;
}
.alpheios-alignment-text-editor-block__header .alpheios-alignment-text-editor-block__header-link {
  display: inline-block;
  padding-right: 15px;
  color: #185F6D;
  cursor: pointer;
  text-decoration: underline;
  text-transform: uppercase;
}

.alpheios-alignment-text-editor-block.alpheios-alignment-editor-container {
  padding: 20px 30px 20px 40px;
}

.alpheios-alignment-editor-text-blocks-container-inner:before, .alpheios-alignment-editor-text-blocks-container-inner:after {
  content: "";
  display: block;
  clear: both;
}

.alpheios-alignment-editor-text-blocks-container .alpheios-alignment-editor-text-blocks-single-container {
  width: 50%;
  float: left;
}

.alpheios-alignment-editor-text-blocks-origin-container {
  padding-right: 10px;
}

.alpheios-alignment-editor-text-blocks-target-container {
  padding-left: 10px;
}

.alpheios-alignment-editor-container-footer {
  text-align: center;
  border-top: 2px solid #757575;
  padding-top: 15px;
}

.alpheios-alignment-editor-header__show-label {
  cursor: pointer;
  font-size: 90%;
  text-decoration: underline;
  color: #185F6D;
}

button.alpheios-actions-menu-button.alpheios-actions-menu-button-with-icon {
  padding: 5px;
  margin: 0 5px;
}
button.alpheios-actions-menu-button.alpheios-actions-menu-button-with-icon .alpheios-alignment-button-icon {
  display: inline-block;
  width: 25px;
  height: 25px;
}
button.alpheios-actions-menu-button.alpheios-actions-menu-button-with-icon .alpheios-alignment-button-icon svg {
  width: 100%;
  height: 100%;
  display: block;
  fill: #fff;
}