.alpheios-alignment-editor-modal-annotations .alpheios-modal-body {
  border: 0;
  padding: 0;
}
.alpheios-alignment-editor-modal-annotations span.alpheios-alignment-editor-red {
  color: #99002a;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-textarea {
  width: 100%;
  min-height: 150px;
  padding: 10px;
  font-size: inherit;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-select {
  margin-bottom: 15px;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list {
  border-collapse: collapse;
  width: 100%;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list td {
  border: 1px solid #ddd;
  padding: 8px;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list td.alpheios-alignment-editor-annotation-list-item__type {
  width: 100px;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list td.alpheios-alignment-editor-annotation-list-item__text {
  max-width: 10vw;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list td.alpheios-alignment-editor-annotation-list-item__text span {
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list .alpheios-alignment-editor-annotation-list-item__full .alpheios-alignment-editor-annotation-list-item__text span {
  text-overflow: inherit;
  overflow: inherit;
  white-space: initial;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list .alpheios-alignment-editor-annotation-list-item__disabled {
  color: #a6a6a6;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list tr:nth-child(even) {
  background-color: #f2f2f2;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list .alpheios-alignment-editor-annotation-list-item__edit {
  text-align: center;
  width: 100px;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list .alpheios-alignment-editor-annotation-list-item__edit span {
  display: inline-block;
  width: 20px;
  height: 20px;
  cursor: pointer;
}
.alpheios-alignment-editor-modal-annotations .alpheios-alignment-editor-annotation-list .alpheios-alignment-editor-annotation-list-item__edit span svg {
  display: block;
  width: 100%;
  height: 100%;
}