.alpheios-alignment-option-item {
  display: inline-block;
  width: 100%;
  vertical-align: top;
  padding-right: 20px;
  margin-bottom: 20px;
}
.alpheios-alignment-option-item > label {
  width: 48%;
  display: inline-block;
  vertical-align: top;
  padding-right: 10px;
}
.alpheios-alignment-option-item > label.alpheios-alignment-option-item__label-invisible {
  visibility: hidden;
}
.alpheios-alignment-option-item input.alpheios-alignment-input,
.alpheios-alignment-option-item .alpheios-alignment-select,
.alpheios-alignment-option-item .alpheios-alignment-radio-block,
.alpheios-alignment-option-item .multiselect.alpheios-alignment-option-item__control {
  display: inline-block;
  width: 50%;
  vertical-align: middle;
}
.alpheios-alignment-option-item p.alpheios-alignment-radio-block {
  margin-top: 0;
  margin-bottom: 0;
}
.alpheios-alignment-option-item p.alpheios-alignment-radio-block > span {
  display: inline-block;
}
.alpheios-alignment-option-item p.alpheios-alignment-radio-block.alpheios-each-line-radio > span {
  display: block;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__select-container span,
.alpheios-alignment-option-item .alpheios-alignment-select-input__input-container span {
  min-width: 160px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__select,
.alpheios-alignment-option-item .alpheios-alignment-select-input__input {
  width: 80%;
  max-width: 300px;
  display: inline-block;
  vertical-align: top;
}
.alpheios-alignment-option-item .alpheios-alignment-select-input__input-description {
  font-size: 90%;
  color: #888;
}
.alpheios-alignment-option-item .alpheios-alignment-option-item__label-text,
.alpheios-alignment-option-item .alpheios-alignment-option-item__label-info {
  display: inline-block;
  vertical-align: middle;
}
.alpheios-alignment-option-item .alpheios-alignment-option-item__label-text.alpheios-alignment-option-item__label-hasinfo {
  max-width: 80%;
}

.alpheios-alignment-option-item__label-info {
  width: 25px;
  height: 25px;
  display: inline-block;
  text-align: center;
  line-height: 25px;
  font-weight: bold;
  border-radius: 20px;
  vertical-align: middle;
  margin-left: 10px;
}

.alpheios-alignment-option-item__label-info {
  color: #fff;
  background: #000;
}

.alpheios-alignment-option-item .alpheios-alignment-option-item__label-container,
.alpheios-alignment-option-item .alpheios-alignment-checkbox-block.alpheios-alignment-option-item__control {
  vertical-align: middle;
}