.alpheios-editor-summary-header {
  font-weight: bold;
}

.alpheios-editor-langs-table {
  border-collapse: collapse;
  width: 100%;
}
.alpheios-editor-langs-table th, .alpheios-editor-langs-table td {
  border: 1px solid #ddd;
  padding: 8px;
}
.alpheios-editor-langs-table tr:nth-child(even) {
  background-color: #f2f2f2;
}
.alpheios-editor-langs-table th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #90a959;
  color: white;
}
.alpheios-editor-langs-table .alpheios-editor-langs-table_lang {
  font-weight: bold;
}

.alpheios-editor-summary-footer {
  text-align: center;
}

.alpheios-editor-summary-show-option {
  padding-top: 10px;
}

.alpheios-editor-summary-show-option-item {
  width: 30px;
  display: inline-block;
  vertical-align: middle;
}
.alpheios-editor-summary-show-option-item .alpheios-alignment-option-item {
  margin-bottom: 0;
}

.alpheios-editor-summary-show-option-label {
  display: inline-block;
  vertical-align: middle;
}

.alpheios-editor-langs-table-tokenized {
  color: #b0b0b0;
}
.alpheios-editor-langs-table-tokenized svg {
  fill: #b0b0b0;
}

.alpheios-editor-langs-table_teicheck span.alpheios-alignment-editor-text-blocks-single__icons svg {
  display: inline-block;
  width: 15px;
  height: 15px;
  fill: #000;
}

.alpheios-alignment-editor-modal-summary .alpheios-modal-body {
  border: 0;
  padding: 10px 0;
}